<template>
  <div class="history-tx-container page-container">
    <balance-block
      :curr-symbol="currSymbol"
      :balance="balance"
      class="balance-block"
    />
    <history-tx-list :transactions="transactions" :tx-loaded="txLoaded" />
  </div>
</template>

<script>
import HistoryTxList from '@/components/HistoryTxList';
import { Toast } from '@/helpers';
import BalanceBlock from '@/components/BalanceBlock';

export default {
  components: {
    'history-tx-list': HistoryTxList,
    'balance-block': BalanceBlock
  },
  props: {
    getBalance: {
      type: Function,
      default: function() {}
    },
    getTxs: {
      type: Function,
      default: function() {}
    },
    currSymbol: {
      type: String,
      default: function() {
        return '';
      }
    }
  },
  data() {
    return {
      transactions: [],
      txLoaded: false,
      balance: ''
    };
  },
  computed: {},
  created() {
    this._getTxs();
    this._getBalance();
  },
  methods: {
    _getTxs() {
      this.getTxs()
        .then(res => {
          this.txLoaded = true;
          this.transactions = res;
        })
        .catch(err => {
          this.txLoaded = true;
          Toast.responseHandler(err, Toast.ERROR);
        });
    },
    _getBalance() {
      this.getBalance()
        .then(res => {
          this.balance = res.toString();
        })
        .catch(err => {
          Toast.responseHandler(err, Toast.ERROR);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'HistoryTxContainer.scss';
</style>
